.loader {
  color: #000;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  width: 5px;
  height: 5px;
  box-sizing: border-box;
  background: currentColor;
  animation: animloader 1s linear infinite;
  position: absolute;
  bottom: 10px;
  right: -5px;
}

@keyframes animloader {
  0% {
    box-shadow: 10px 0 #000, 20px 0 #000;
  }

  50% {
    box-shadow: 10px 0 #000, 20px 0 #000;
  }

  100% {
    box-shadow: 10px 0 #000, 20px 0 #000;
  }
}

.breed-select {
  cursor: pointer;
  border: 1px solid #000;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 14px;
  display: block;
}

* {
  font-family: Arial, Helvetica, sans-serif;
}

